<template>
    <div>
        <Header></Header>
        <section class="test_bg">
            <div class="container">
                <div class="jobs_title">
                    Muvaffaqiyatingizni
                    sevgan kasbingizni
                    izlashdan boshlang!
                </div>
                <div class="jobs_desc">
                    Keling, birgalikda ozingizga mos kelajak kasbingizni topamiz - bu bepul va bir necha daqiqa
                    vaqtingizni oladi. Boshlaymizmi?
                </div>
                <div class="jobs_main">
                    <div class="form_filt">
                        <div class="search_input">
                            <svg width="20" height="20" viewBox="0 0 20 20" fill="none"
                                 xmlns="http://www.w3.org/2000/svg">
                                <path d="M9.16666 15.8334C12.8486 15.8334 15.8334 12.8486 15.8334 9.16666C15.8334 5.48476 12.8486 2.5 9.16666 2.5C5.48476 2.5 2.5 5.48476 2.5 9.16666C2.5 12.8486 5.48476 15.8334 9.16666 15.8334Z"
                                      stroke="#323133" stroke-width="1.8" stroke-linecap="round"
                                      stroke-linejoin="round"/>
                                <path d="M17.5 17.5001L13.875 13.8751" stroke="#323133" stroke-width="1.8"
                                      stroke-linecap="round" stroke-linejoin="round"/>
                            </svg>

                            <input @input="getProf_search()" v-model="search" type="text" placeholder="Kelajak kasbingizni izlang">
                        </div>
                        <div class="selects">
                            <select  v-model="selects_item" @input="ser_select_cat()">
                                <option v-for="ser in jobs_category" :key="ser"   :value="ser.id">{{ser.title}}</option>
                            </select>
                        </div>
                        <div class="selects">
                            <select  v-model="selects_price" @input="ser_select_price()" id="options">
                                <option value="max_price">Eng yuqori maosh</option>
                                <option value="min_price">Eng kam maosh</option>
                            </select>
                        </div>
                    </div>
                    <div class="row">
                        <div class="col-xl-6 card_mains_job" v-for="(item,index) in jobs.results" :key="index">

                            <div class="jobs_cards">
                                <div>
                                    <img :src="item.cover_image_url" alt="">
                                </div>
                                <div>
                                    <div class="job-title">
                                        {{item.title}}
                                    </div>
                                    <div class="job_des" v-html="item.workplace">

                                   </div>
                                    <div class="job_price">
                                        <span>  Oylik maoshi:</span>
                                       {{item.salary_range}} som
                                    </div>
                                    <div class="jobs_link">
                                        <router-link :to="`/jobs/${item.id}`">
                                            Batafsil tanishish
                                            <div>
                                                <svg width="24" height="24" viewBox="0 0 24 24" fill="none"
                                                     xmlns="http://www.w3.org/2000/svg">
                                                    <path d="M10 7L15 12L10 17" stroke="#8F51E6" stroke-width="2"
                                                          stroke-linecap="round" stroke-linejoin="round"/>
                                                </svg>

                                            </div>
                                        </router-link>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="pagenationss">
                        <b-pagination
                                v-model="page"
                                :total-rows="jobs.total_pages"
                                :per-page="perPage"
                                first-number
                                last-number
                                aria-controls="my-table"></b-pagination>
                    </div>
                </div>
            </div>
        </section>
        <Footer></Footer>
    </div>
</template>
<script>
    import Header from "../../components/Header";
    import Footer from "../../components/Footer"

    export default {
        components: {
            Header,
            Footer
        },
        data(){
          return{
              jobs:[],
              page: 1,
              total: 2,
              perPage: 1,
              search:'',
              jobs_category:[],
              selects_item:'',
              selects_price:''
          }
        },
        mounted() {
            this.$axios.get('/v1/professions-web/?page=1', {
                headers: {
                    'Authorization': `Bearer ${localStorage.getItem('token')}`,
                    'Accept-Language': 'oz'
                }
            })
                .then(res => {
                    this.jobs = res.data
                })
            this.$axios.get(`/v1/profession-categories/`, {
                headers: {
                    'Authorization': `Bearer ${localStorage.getItem('token')}`,
                    'Accept-Language': 'oz'
                }
            })
                .then(res=>{
                    this.jobs_category = res.data
                })
        },
        watch: {
            page(page) {
                this.$axios.get(`/v1/professions-web/?page=${page}`, {
                    headers: {
                        'Authorization': `Bearer ${localStorage.getItem('token')}`,
                        'Accept-Language': 'oz'
                    }
                })
                    .then(res => {
                        this.jobs = res.data
                    })

            },
        },
        methods:{
            getProf_search(){
                this.$axios.get(`/v1/professions-web/?search=${this.search}`, {
                    headers: {
                        'Authorization': `Bearer ${localStorage.getItem('token')}`,
                        'Accept-Language': 'oz'
                    }
                })
                    .then(res=>{
                        this.jobs = res.data
                    })
            },
            ser_select_cat(){
                this.$axios.get(`/v1/professions-web/?professsion-category=${this.selects_item}`, {
                    headers: {
                        'Authorization': `Bearer ${localStorage.getItem('token')}`,
                        'Accept-Language': 'oz'
                    }
                })
                    .then(res=>{
                        this.jobs = res.data
                    })
            },
            ser_select_price(){
                this.$axios.get(`/v1/professions-web/?ordering=${this.selects_price}`, {
                    headers: {
                        'Authorization': `Bearer ${localStorage.getItem('token')}`,
                        'Accept-Language': 'oz'
                    }
                })
                    .then(res=>{
                        this.jobs = res.data
                    })
            }
        }
    }
</script>