                                          fill="#323133"/>
                                </svg>
                            </div>
                            Manzil:
                            {{university_one.university.short_description}}
                        </div>
                        <div class="sex_card_location call_icon">

                            <div>

                                <svg width="16" height="16" viewBox="0 0 16 16" fill="none"
                                     xmlns="http://www.w3.org/2000/svg">
                                    <path
                                            d="M0 1C0 0.734784 0.105357 0.48043 0.292893 0.292893C0.48043 0.105357 0.734784 0 1 0H3.153C3.38971 0.000108969 3.6187 0.0841807 3.79924 0.23726C3.97979 0.390339 4.10018 0.602499 4.139 0.836L4.879 5.271C4.91436 5.48222 4.88097 5.69921 4.78376 5.89003C4.68655 6.08085 4.53065 6.23543 4.339 6.331L2.791 7.104C3.34611 8.47965 4.17283 9.72928 5.22178 10.7782C6.27072 11.8272 7.52035 12.6539 8.896 13.209L9.67 11.661C9.76552 11.4695 9.91994 11.3138 10.1106 11.2166C10.3012 11.1194 10.5179 11.0859 10.729 11.121L15.164 11.861C15.3975 11.8998 15.6097 12.0202 15.7627 12.2008C15.9158 12.3813 15.9999 12.6103 16 12.847V15C16 15.2652 15.8946 15.5196 15.7071 15.7071C15.5196 15.8946 15.2652 16 15 16H13C5.82 16 0 10.18 0 3V1Z"
                                            fill="#323133"/>
                                </svg>
                            </div>

                        </div>
                    </div>
                    <div class="main_fac">
                        Asosiy yonalishlar
                    </div>
                    <div class="fac_text">
                            <span v-for="(res,ind) in university_one.related" :key="ind">
                                {{res.title}}
                            </span>
                    </div>
                </div>

            </div>
        </div>
        <section class="test_bg">
            <div class="container">
                <div class="row">
                    <div class="col-xl-4 align-content-center">
                        <img src="../assets/images/leftnews.png" alt="">
                    </div>
                    <div class="col-xl-4">
                        <div class="univer_text text-center">
                            Yangiliklar
                        </div>
                        <div class="univer_desc text-center">
                            Ozingizga yoqqan fanlarni tanlang va kalkulyator sizga
                            kasb tanlashda yordam beradi
                        </div>
                    </div>
                    <div class="col-xl-4 align-content-center">
                        <img src="../assets/images/rigtnews.png" alt="">
                    </div>
                </div>
                <div class="main_univers">
                    <div class="row">
                        <div class="col-xl-4" v-for="(item,index) in university.results" :key="index">
                            <router-link to="/" class="text-decoration-none">
                                <div class="news_card">
                                    <div class="news_img">
                                        <img :src="item.cover_url" alt="">
                                    </div>
                                    <div class="news_title">
                                        {{item.title}}
                                    </div>

                                    <!--                                    <div class="news_date">-->
                                    <!--                                        10-iyun, 2023  12:36-->
                                    <!--                                    </div>-->
                                </div>
                            </router-link>
                        </div>
                    </div>
                </div>
                <div class="pagenationss">
                    <b-pagination
                            v-model="page"
                            :total-rows="university.total_pages"
                            :per-page="perPage"
                            first-number
                            last-number
                            aria-controls="my-table"></b-pagination>
                </div>
            </div>
        </section>
        <Footer></Footer>
    </div>
</template>
<script>
    import Header from "../components/Header";
    import Footer from "../components/Footer"

    export default {
        components: {
            Header,
            Footer
        },
        data() {
            return {
                university: [],
                is_active: 0,
                page: 1,
                total: 2,
                perPage: 1,
                university_one:[],
                show_cards:false
            }
        },
        mounted() {
            this.$axios.get('/news/?is_world=1&&page=1', {
                headers: {
                    'Authorization': `Bearer ${localStorage.getItem('token')}`,
                    'Accept-Language': 'oz'
                }
            })
                .then(res => {
                    this.university = res.data
                })
        },
        methods: {
            get_univer_item(item){
                this.$axios.get(`/news/${item.id}`, {
                    headers: {
                        'Authorization': `Bearer ${localStorage.getItem('token')}`,
                        'Accept-Language': 'oz'
                    }
                })
                    .then(res => {
                        this.university_one = res.data
                        this.show_cards=true
                    })
            },
            getIsCountry() {
                this.$axios.get('/news/?is_country=1&&page=1', {
                    headers: {
                        'Authorization': `Bearer ${localStorage.getItem('token')}`,
                        'Accept-Language': 'oz'
                    }
                })
                    .then(res => {
                        this.university = res.data
                    })
            },
            getIsworld() {
                this.$axios.get('/news/?is_world=1&&page=1', {
                    headers: {
                        'Authorization': `Bearer ${localStorage.getItem('token')}`,
                        'Accept-Language': 'oz'
                    }
                })
                    .then(res => {
                        this.university = res.data
                    })
            },
            getProfi() {
                this.$axios.get('/news/?is_prof_education=1&&page=1', {
                    headers: {
                        'Authorization': `Bearer ${localStorage.getItem('token')}`,
                        'Accept-Language': 'oz'
                    }
                })
                    .then(res => {
                        this.university = res.data
                    })
            }
        },
        watch: {
            page(page) {
                this.$axios.get(`/news/?is_world=1&&page=${page}`, {
                    headers: {
                        'Authorization': `Bearer ${localStorage.getItem('token')}`,
                        'Accept-Language': 'oz'
                    }
                })
                    .then(res => {
                        this.university = res.data
                    })

            },
        },
    }
</script>