<template>
    <div>
        <Header></Header>
        <section class="login_bg">
            <div class="main_test_result">

            </div>
            <div  class="container">
                <div class="card_fathers" v-if="start_test">
                    <div class="start_card">
                        <div>
                            <div class="start_card_tit">
                                Ijtimoiy intellekt sorovnomasi
                                (Hall metodologiyasini ozgartirilgani Rezapkina)
                            </div>
                            <div class="start_card_des">
                                Metodologiya 30 ta bayonotdan iborat bo'lib, insonning to'liq kasbiy va shaxsiy
                                rivojlanishining eng muhim sharti bo'lgan ijtimoiy intellektning tarkibiy qismlarini
                                aniqlashga imkon beradi ("oz-ozini anglash", "ozini o'zi boshqarish", "empatiya",
                                "muloqot", "oz-ozini motivatsiyalash").
                            </div>
                        </div>
                        <div class="quiz_firsts">
                            <div class="num_f">
                                <div class="num_one">
                                    1
                                </div>
                                <div class="num_title">
                                    Testni boshlash uchun Testni boshlash tugmasini bosing;
                                </div>
                            </div>
                            <div class="num_f">
                                <div class="num_one">
                                    2
                                </div>
                                <div class="num_title">
                                    Berilgan savoldan kelib chiqib HA yoki YOQ javobni belginlang.
                                </div>
                            </div>
                        </div>
                        <div class="notes_main">
                            Eslatma. Test davomida sizga 30 ta savol beriladi. Keyingi savolga fakatgina birinchi
                            savolga javob berilgandan song otiladi.
                        </div>
                        <div class="start_buttons">
                            <button class="starts_cencel" @click="$router.push('/test_category')">
                                Bekor qilish
                            </button>
                            <button class="starts_start" @click="startFunction()">
                                Testni boshlash
                            </button>
                        </div>
                    </div>
                </div>
                <div class="card_fathers " v-if="!start_test">
                    <div class="start_card">
                        <div class="start_card_header">
                            <div class="quiz_time">

                                <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                                    <path d="M17.6177 5.9681L18.7175 4.86827C18.9128 4.67301 19.2294 4.67301 19.4247 4.86827L20.1317 5.57537C20.327 5.77064 20.327 6.08722 20.1317 6.28248L19.0319 7.38231C20.2635 8.92199 21 10.875 21 13C21 17.9706 16.9706 22 12 22C7.02944 22 3 17.9706 3 13C3 8.02944 7.02944 4 12 4C14.125 4 16.078 4.73647 17.6177 5.9681ZM11.5 8C11.2239 8 11 8.22386 11 8.5V13.5C11 13.7761 11.2239 14 11.5 14H12.5C12.7761 14 13 13.7761 13 13.5V8.5C13 8.22386 12.7761 8 12.5 8H11.5ZM8 1.5C8 1.22386 8.22386 1 8.5 1H15.5C15.7761 1 16 1.22386 16 1.5V2.5C16 2.77614 15.7761 3 15.5 3H8.5C8.22386 3 8 2.77614 8 2.5V1.5Z" fill="#FA8F42"/>
                                </svg>

                                33:10
                            </div>
                            <div class="quiz_count">
                                {{index_main+1}} / {{tests.questions.length}}
                            </div>
                            <div>
                                <button class="fineshed_btn" @click="finished_tests()">
                                    Testni tugatish
                                </button>
                            </div>
                        </div>
                        <div v-show="index_main==index" v-for="(quiz,index) in tests.questions" :key="index">
                            <div >
                                <div class="quiz">
                                    {{quiz.title}}
                                </div>
                                <div class="quiz_btn">
                                    <button class="yes_btn" :class="{'active':is_active==1}" @click="is_active=1">
                                        Ha
                                    </button>
                                    <button :class="{'active':is_active==0}" @click="is_active=0">
                                        Yo'q
                                    </button>
                                </div>
                            </div>
                            <div class="bottom_nextbtn">

                                <button @click="next_shows(quiz,index)">
                                    Davom etish
                                </button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>


            <img src="../../assets/images/vec1.png" class="vec_1" alt="">
            <img src="../../assets/images/vec2.png" class="vec_2" alt="">
            <img src="../../assets/images/vec3.png" class="vec_3" alt="">

        </section>
        <Footer></Footer>

    </div>
</template>
<script>
    import Header from "../../components/Header";
    import Footer from "../../components/Footer"
    export default {
        components:{
          Header,
          Footer
        },
        data(){
            return{
                start_test:true,
                is_active:'',
                tests:[],
                index_main:0,
                answeer:[]
            }
        },

        methods:{
            next_shows(quiz){
              this.index_main++
              this.answeer.push({
                  "question_id":quiz.id,
                  "answer":this.is_active
              })
                console.log(this.answeer)
            },
            startFunction(){
                this.$axios.post('/test/test-start/',{
                    "test_category_id":this.$route.params.id
                },
                    {
                        headers: {
                            'Authorization': `Bearer ${localStorage.getItem('token')}`,
                            'Accept-Language':'oz'
                        }
                    }
                )
                    .then(res=>{
                        this.start_test=false
                        this.tests=res.data
                    })
            },
            finished_tests(){


                this.$axios.post('/test/test-finish/',{
                    "answers":this.answeer,
                    "result_id":this.tests.result_id
                    },
                    {
                        headers: {
                            'Authorization': `Bearer ${localStorage.getItem('token')}`,
                            'Accept-Language':'oz'
                        }
                    }
                )
                    .then(res=>{
                        this.tests=res.data
                    })
            }
        }

    }
</script>