<script>
    import Header from "../../components/Header";
    import Footer from "../../components/Footer";

    export default {
        components: {
            Header,
            Footer
        },
        data(){
            return{
                jobs:[]
            }
        },
        mounted() {
            this.$axios.get(`/v1/professions-web/${this.$route.params.id}`, {
                headers: {
                    'Authorization': `Bearer ${localStorage.getItem('token')}`,
                    'Accept-Language': 'oz'
                }
            })
                .then(res => {
                    this.jobs = res.data
                })
        },
    }
</script>